@import url(https://fonts.googleapis.com/css?family=Ubuntu:R|Bebas+Neue|Mrs+Sheppards|Catamaran:800|Poppins:600,R);
body{margin:0;font-family:-apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen", "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue", sans-serif;-webkit-font-smoothing:antialiased;-moz-osx-font-smoothing:graysscale;background-color:#e9e9e9;color:#323232}code{font-family:source-code-pro, Menlo, Monaco, Consolas, "Courier New", monospace}section{font-family:"Ubuntu", sans-serif;min-height:100vh;height:auto;display:flex;justify-content:center;align-items:center}

#footer{position:absolute;bottom:0;width:100%;height:15vh;padding-bottom:1rem}@media screen and (max-width: 1200px){#footer{height:5vh}}#external_links{width:100%}@media screen and (max-width: 1200px){#external_links{display:none}}.image_links{margin:0 2rem}.icons{color:#181818;opacity:0.75;font-size:2rem;transition:opacity 0.3s ease, -webkit-transform 0.3s ease;transition:opacity 0.3s ease, transform 0.3s ease;transition:opacity 0.3s ease, transform 0.3s ease, -webkit-transform 0.3s ease}.icons:hover{opacity:1;-webkit-transform:translateY(-5px);transform:translateY(-5px)}

#home{font-size:5em;min-height:100vh;text-align:center;font-family:'Ubuntu', sans-serif}#title{display:flex;align-items:center;justify-content:center;height:72.5vh}@media screen and (max-width: 1200px){#title{flex-direction:column}}#name{height:auto}#name p{font-size:10.5vw}@media screen and (max-width: 1200px){#name p{font-size:8.5rem}}@media screen and (max-width: 960px){#name p{font-size:6rem}}@media screen and (max-width: 600px){#name p{font-size:4.5rem}}#brush_stroke{background:url(/static/media/brush-stroke.d77f61fe.png);background-repeat:no-repeat;background-size:100% 95%;padding:1rem 7.5rem}@media screen and (max-width: 600px){#brush_stroke{padding:1rem 3rem}}#self-portrait{width:22.5vw;margin-left:2.5rem;border-radius:2rem;box-shadow:10px 10px rgba(122,63,181,0.5);transition:box-shadow 0.3s ease}@media screen and (max-width: 1200px){#self-portrait{margin-left:0;-webkit-transform:translateY(-100px);transform:translateY(-100px);width:24.5rem}}@media screen and (max-width: 960px){#self-portrait{-webkit-transform:translateY(-50px);transform:translateY(-50px);width:22rem}}@media screen and (max-width: 600px){#self-portrait{width:18rem}}#self-portrait:hover{box-shadow:5px 5px rgba(122,63,181,0.5)}@font-face{font-family:"Unforgotton";src:url(/static/media/unforgotten.regular.0e4eebb3.ttf)}

@media screen and (max-width: 1200px){#base{flex-direction:column}}.introduction{width:35vw;margin-left:5rem}.introduction h1{font-size:3rem;text-align:left}.introduction p{font-size:1.5rem;text-align:left}@media screen and (max-width: 1200px){.introduction{margin:0;width:75vw}}@media screen and (max-width: 850px){.introduction{width:80vw}.introduction h1{font-size:2.5rem}.introduction p{font-size:1.25rem}}@media screen and (max-width: 480px){.introduction h1{font-size:2rem}.introduction p{font-size:0.75rem}}

.experience{flex-direction:column}#experience_header{margin-bottom:2rem;-webkit-animation:hovering 1.5s ease;animation:hovering 1.5s ease;-webkit-animation-iteration-count:infinite;animation-iteration-count:infinite}#polaroids{width:80%;text-align:center}#logo{width:15rem;padding:20px}@media screen and (max-width: 600px){#logo{width:10rem}}#postcard{display:flex;min-height:15rem;align-items:center;flex-direction:column}#postcard p{padding:20px;width:25rem;font-family:'Poppins', sans-serif}@media screen and (max-width: 600px){#postcard p{border-left:0}}@media screen and (max-width: 600px){#postcard{flex-direction:column}}.info_card{width:25rem}@media screen and (max-width: 600px){.info_card{width:20rem}}.info_card:hover{cursor:pointer}.typography p{font-family:'Poppins', sans-serif;font-weight:600;margin:5px}@-webkit-keyframes hovering{0%{-webkit-transform:translateX(0);transform:translateX(0)}50%{-webkit-transform:translateY(-10px);transform:translateY(-10px)}100%{-webkit-transform:translateX(0);transform:translateX(0)}}@keyframes hovering{0%{-webkit-transform:translateX(0);transform:translateX(0)}50%{-webkit-transform:translateY(-10px);transform:translateY(-10px)}100%{-webkit-transform:translateX(0);transform:translateX(0)}}

@media screen and (max-width: 1024px){#skills_section{flex-direction:column}}#skills{width:60vw;text-align:left}@media screen and (max-width: 1024px){#skills{width:85vw}}@media screen and (max-width: 600px){#skills{width:90vw}}#coding li{background-color:rgba(16,175,95,0.555);border-radius:0.15em;margin:0.5% 1%}#interests li{background-color:rgba(236,73,73,0.39);border-radius:0.15em;margin:0.5% 1%}.categories{list-style-type:none}.categories h1{font-size:2rem;margin-bottom:1rem;font-family:"Bebas Neue", sans-serif;letter-spacing:2.5px}@media screen and (max-width: 600px){.categories h1{font-size:1.5rem;letter-spacing:2px}}@media screen and (max-width: 450px){.categories h1{font-size:1rem;letter-spacing:2px}}.languageProficiency{padding:0.25em;background-color:rgba(44,196,196,0.39);border-radius:0.15em;font-size:1em;display:inline-block}li{padding:0.75% 1%;display:inline-block;font-size:1.5rem}@media screen and (max-width: 1024px){li{font-size:1rem}}@media screen and (max-width: 600px){li{font-size:0.75rem}}

#header{display:flex;position:absolute;align-items:center;width:100%;z-index:1;height:12.5vh}#web_navigation{top:0;display:inherit;height:inherit;opacity:100%;align-items:inherit;width:100%;position:relative}@media screen and (max-width: 1200px){#web_navigation{display:none}}#returnHome{font-family:"Mrs Sheppards", sans-serif;font-size:3em;text-decoration:none;color:#323232;opacity:0.75;margin-left:5rem;transition:opacity 300ms ease}#returnHome:hover{opacity:1}#links{position:absolute;display:inherit;align-items:inherit;right:0;top:0;margin-right:2rem;height:100%}#mobile_navigation{height:100%;background:whitesmoke;display:flex;position:fixed;z-index:1;top:0;left:0;justify-content:center;align-items:center;flex-direction:column;text-align:center;overflow-x:hidden;transition:0.5s ease}@media screen and (min-width: 1200px){#mobile_navigation{display:none}}#mobile_links_ul{padding:0}#mobile_links_ul li{display:block;padding:1rem 0}#mobile_external_links{margin-top:25px;height:6vh;min-width:100%;overflow:hidden;transition:opacity 0.3s ease}@media screen and (max-width: 960px){#mobile_external_links{height:5vh}}@media screen and (max-width: 600px){#mobile_external_links{height:4vh}}#mobile_nav_trigger{opacity:1;transition:opacity 0.2 ease}#mobile_nav_trigger{position:fixed;top:0;left:0;width:3rem;-webkit-transform:translate(25px, 25px);transform:translate(25px, 25px);transition:opacity 0.25s ease}@media screen and (max-width: 960px){#mobile_nav_trigger{width:2.5rem}}@media screen and (max-width: 600px){#mobile_nav_trigger{width:2rem}}#mobile_nav_trigger:hover{cursor:pointer}.link{margin:0 2rem;padding:0.25em 0.5rem;font-size:1.75rem;font-family:"Bebas Neue", sans-serif;letter-spacing:2.5px;position:relative;-webkit-user-select:none;-moz-user-select:none;-ms-user-select:none;user-select:none;outline:none;color:#181818;z-index:2;text-decoration:none}.link::after{content:"";position:absolute;left:50%;bottom:11px;z-index:-1;width:0;height:0.3em;opacity:0;background:rgba(143,219,181,0.856);transition:all 350ms cubic-bezier(0.5, 1.6, 0.15, 0.9)}.link:hover::after{left:0;width:100%;opacity:1}.mobile_links{color:#181818;font-family:"persona5";letter-spacing:5px;text-decoration:none;font-size:5rem}@media screen and (max-width: 960px){.mobile_links{font-size:4rem}}@media screen and (max-width: 600px){.mobile_links{font-size:3rem}}.image_links_mobile{margin:0 2rem}@media screen and (max-width: 960px){.image_links_mobile{margin:0 1rem}}.mobile_icon{color:#181818;opacity:0.75;font-size:6vh;transition:opacity 0.3s ease}@media screen and (max-width: 960px){.mobile_icon{font-size:5vh}}@media screen and (max-width: 600px){.mobile_icon{font-size:4vh}}.mobile_icon:hover{opacity:1}@font-face{font-family:"persona5";src:url(/static/media/persona5.13fd6f34.ttf)}

